@charset "UTF-8";

@import 'partials/_mixin';
@import 'partials/_variables';

.main-container {
	flex: 1 1 100%;
	display: flex;
}

#page.page-main {
	max-width: 100%;
}

.main-visual-slides {
	position: relative;
	height: 100vh;

	.slide-list {
		.slide-item {
			a {
				display: block;
				height: 100%;
			}

			img {
				width: 100%;
				height: 100%;
				margin: 0 auto;
				object-fit: cover;
			}
		}
	}

	.swiper-pagination {
		display: flex;
		column-gap: 8px;
		position: absolute;
		top: auto;
		bottom: 24px;
		left: 50%;
		z-index: 1;
		width: auto;
		height: auto;
		padding: 12px 24px;
		border-radius: 24px;
		background-color: rgba(185, 185, 185, 0.365);
		transform: translateX(-50%);

		.swiper-pagination-bullet {
			width: 8px;
			height: 8px;
			margin: 0;
			border-radius: 8px;
			background: var(--color-base-100);
			opacity: 0.5;
			transition: width 0.2s ease-in-out, opacity 0.2s ease-in-out;
		}

		.swiper-pagination-bullet-active {
			width: 32px;
			opacity: 1;
		}
	}

	[class*="swiper-button"] {
		position: absolute;
		top: calc(50% - 32px);
		z-index: 1;

		&::before {
			content: '';
			display: block;
			width: 64px;
			height: 64px;
			mask-repeat: no-repeat;
			mask-size: cover;
			mask-position: 50%;
			background-color: var(--color-base-100);
			opacity: 0.5;
			cursor: pointer;
		}

		.btn-text {
			@include hidden;
		}

		// &.swiper-button-disabled {
		// 	display: none;
		// }

		&.swiper-button-next {
			right: 0;
			&::before { mask-image: icon(arrow-right); }
		}

		&.swiper-button-prev {
			left: 0;
			&::before { mask-image: icon(arrow-left); }
		}
	}
}

// 공지사항 슬라이드
.main-notice-slides {
	position: relative;

	.slide-func {
		.swiper-pagination {
			display: flex;
			column-gap: 8px;
			position: absolute;
			top: 16px;
			right: 16px;
			bottom: auto;
			left: auto;
			z-index: 1;
			width: auto;
			height: auto;
			padding: 12px;
			background-color: rgba(185, 185, 185, 0.365);

			.swiper-pagination-bullet {
				width: 8px;
				height: 8px;
				margin: 0;
				border-radius: 8px;
				background: var(--color-base-100);
				opacity: 0.5;
				transition: width 0.2s ease-in-out, opacity 0.2s ease-in-out;
			}

			.swiper-pagination-bullet-active {
				width: 32px;
				opacity: 1;
			}
		}

		.swiper-button-prev,
		.swiper-button-next {
			position: absolute;
			top: calc(50% - 32px);
			z-index: 1;

			&::after {
				content: '';
				width: 64px;
				height: 64px;
				background-color: var(--color-base-100);
				mask-repeat: no-repeat;
				mask-size: cover;
				mask-position: center;
			}
		}

		.swiper-button-prev::after {
			mask-image: icon(chevron-left);
		}

		.swiper-button-next::after {
			mask-image: icon(chevron-right);
		}

		// .swiper-button-prev.swiper-button-disabled,
		// .swiper-button-next.swiper-button-disabled {}
	}
}